import React from 'react'
import Bubble from '../components/bubble'
import NavBar from '../components/NavBar'
import Subble from '../components/subble'
import IntroSubble from '../components/IntroSubble'
import ProjectSubble from '../components/projectSubble'
const TallyTabPage = () => (
  <Bubble>
    <NavBar />
    <Subble stylingClass = "subble"><h1>TallyTab: <i>Never lose track of your tabs again!</i></h1></Subble>
    <IntroSubble />
  </Bubble>
)

export default TallyTabPage
