import React from 'react'
import Subble from './subble'

import './subble.scss'

const NavBar = () => (
  <Subble stylingClass="navBarSubble">
    <Subble stylingClass = "navBarItemSubble"><a href="/">Home</a></Subble>
    <Subble stylingClass = "navBarItemSubble"><a href="/about/">About</a></Subble>
    <Subble stylingClass = "navBarItemSubble"><a href="/projects/">Projects</a></Subble>
    <Subble stylingClass = "navBarItemSubble"><a href="/contact/">Contact</a></Subble>
  </Subble>
)

export default NavBar
